.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loggedinUser {
  padding:4px;  
  font-weight: bold;
}

.smaller-input {
  font-size: 12px;
  padding: 2px 5px;
}

.content {
  /* padding-top: 70px; */
  min-height: calc(100vh - 150px);  
}

.header {
  color: brown;
  background-color: black;
  font-Style: 'italic';
  font-weight: bold;
  font-size: 22;
  /* text-align: right */
}

.Title {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: baseline;
  padding: 1em;
  margin-bottom: 2em;
  background-color:firebrick;
  color: black;
  font-weight: bold;
}

.footer {
  color:black;
  background-color:firebrick;
  font-Style: 'italic';
  font-weight: bold;
  font-size: 16;
  text-align: center;
}

.navbar {
  background-color: goldenrod;
  font-size: 10;
  font-weight: bold;
}

.components {
  /* font-style: oblique; */
  background-color:lightgrey;
}

.bold-text {
  font-weight: bold;
}

.alignleft { 
  text-align: left;  
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  background-color: peru;
}

.alignright {  
  text-align: right;  
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  background-color:peru;
}

.textfont {
  font-size: medium;
}

.footerimage {  
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.shadowtext {
  text-shadow: 2px 2px 2px peru;
}

.background-yellow {
  background-color: lightpink;
}

.fontStyle {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-emphasis-color: yellow;
}